  bottom: 0px;
  left: 0px;
  background: #3ECC29;
  border-radius: 0px;
}

.form-wrapper form .checkbox-wrapper .checkbox:checked + .checkbox-custom::after {
  content: '';
  background: url("../img/ok.svg");
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 10px;
  height: 10px;
